        this.$refs.cropper.startCrop() 开始截图
        this.$refs.cropper.stopCrop() 停止截图
        this.$refs.cropper.clearCrop() 清除截图
        this.$refs.cropper.changeScale() 修改图片大小 正数为变大 负数变小
