        // cropAxis
        this.$refs.cropper.getCropAxis((data) => {
            this.cropAxis = data
        })
        },
        getImgAxis() {
        // ImgAxis
        this.$refs.cropper.getImgAxis((data) => {
            this.imgAxis = data
        })
        },
        clearCrop() {
