                    //     imgUrl.push(`/getFile?file_path=${item}`)
                    //     let bigImgArr = item.split('/min_')
                    //     let bigImgStr = bigImgArr.join('/')
                    //     imgDataBig.push(`/getFile?file_path=${bigImgStr}`)
                    // })
                    
                    // this.imgDataBig = imgDataBig
                },
